	module.exports = {"data":{"markdownRemark":{"html":"<p>A few days ago, this super interesting comment popped up in my Twitter feed:</p>\n<blockquote>\n<p>It’s hard to build a community based on professional feedback in a field where good aesthetics are often praised more than good thinking.</p>\n<p>– Edwin de Jongh, Designer News user</p>\n</blockquote>\n<p>It took a little doing but I tracked it down to a comment on <a href=\"https://api.designernews.co/stories/54913-what-happened-to-the-dribbble-comment-section-this-is-ridiculous\">Designer News about what’s happened to the Dribble comments section</a>. And the conversation (the commiserating) got me thinking, aren’t we as instructional designers, just as bad?</p>\n<h2>We Need Critique</h2>\n<p>Let me be the first to say that I’m guilty of it myself. Especially when you’re new to a community or that community has no reference for critique, it can be impossible or at least taboo to jump in and begin a serious (but respectful) critique. Critique requires a space where everyone involved feels safe enough to critique and be critiqued. Where people trust that their work and not their person will be critiqued (and not criticized or attacked). And, frankly, it also requires a community that values critique rather than just praise. And that’s the tough part in a community where “aesthetics are often praised more than good thinking.” There’s nothing wrong with praise. If you like something, you should take some time to express your appreciation. But if there’s anything I’ve learned from binge watching the <a href=\"https://www.youtube.com/user/pbsideachannel\">Idea Channel</a> for several weeks, it’s that we (I) need to begin thinking critically even (or perhaps especially) about the things that we(I) enjoy. Why did I like that interaction? What about that design was delightful? What was the thinking behind it that made it work? Or, conversely, if you don’t like a thing, think about why you didn’t like it. Your gut reaction isn’t enough. If you’ve read my blog at all, you’ll know that I am a huge (HUGE) advocate of making things and putting them out there. Part of that process, especially if your part of a community of makers, should be receiving good critique on your design, the technical aspects and the design thinking behind it, so that you can get better. Critique is important because it helps the designer to better articulate their process and their thinking. For example, if I’m reviewing a short course made by someone in the elearning community and I critique (never attack) a click-and-reveal interaction as being and unnecessary hindrance to information and suggest, instead, that the designer use separate slides for each piece of information in order to facilitate better pacing, or finds some way to put that information outside the main screen real estate and suggest an interaction where participants can actively use the information, that gives the designer the opportunity to articulate why they choose the click-and-reveal interaction. Was it a client request? Did it just seem easier?  Whether or not the designer makes a change, the critique allows (forces) them to think through the decisions they made.</p>\n<h2>Our Communities Don’t Have It</h2>\n<p>So, we need to make things, and we need to critique them. I tend to do a lot of that myself, but I have been on the lookout for a community that would help, but… I’ve yet to find one. There are <a href=\"/blog/5-reasons-why-instructional-designers-should-make-stuff/\">precious few maker communities in instructional design/L&#x26;D anyway</a>, and the one’s that exist, while they nail being welcoming, completely fail on the critique aspect. Why? I think it goes back to what I wrote earlier. In the service of making people feel comfortable and safe sharing (which is a really important goal), no one has felt safe critiquing another person’s work. It seems taboo. Connect that with the fact that the most popular maker community is connected to a particular authoring tool business and you can well imagine that there isn’t much of an interest in pushing critique, which can make people feel uncomfortable.</p>\n<h2>How Do We Get It?</h2>\n<p>Maybe I’m only used to critique because of creative writing workshops in college. Do instructional design communities have these kinds of workshops? Maybe they should. What do you think? How can we encourage more critique in the ID maker communities?</p>","frontmatter":{"date":"September 03, 2015","path":"/blog/giving-critical-feedback-we-need-more-of-it","title":"Giving Critical Feedback: We Need More of It","tags":["critique","feedback","praise"],"categories":["Showing my Work","Tips"],"featured":{"childImageSharp":{"sizes":{"base64":"data:image/png;base64,","aspectRatio":2,"src":"/static/Giving-feedback-b2d1c382ee4cdf661a8e674741402eb3-81d39.png","srcSet":"/static/Giving-feedback-b2d1c382ee4cdf661a8e674741402eb3-9a8d1.png 203w,\n/static/Giving-feedback-b2d1c382ee4cdf661a8e674741402eb3-c53f9.png 405w,\n/static/Giving-feedback-b2d1c382ee4cdf661a8e674741402eb3-81d39.png 810w,\n/static/Giving-feedback-b2d1c382ee4cdf661a8e674741402eb3-fda19.png 1024w","sizes":"(max-width: 810px) 100vw, 810px"}}}}}},"pathContext":{}}

